@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,400);
body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient( 
    100deg,
    rgb(136 255 240) 30%,
    rgb(230, 255, 0) 120% 
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.nb {
  padding-left: 50px;
  padding-right: 50px;
}

.new-dogs {
  border-radius: 15px;
  display: block;
  position: fixed;
  bottom: 0;
  margin: 20px;
  cursor: pointer;
  right: 0;
  height: 50px;
  color: white;
  width: 150px;
  text-transform: uppercase;
  line-height: 50px;
  text-align: center;
  background: #343a40;
}

.Dog .dog-container {
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}
.Dog .dog-box-img {
  display: flex;
  align-items: center;
  width: 250px;
  height: 250px;
  max-height: 250px;
  overflow: hidden;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-filter: grayscale();
          filter: grayscale();
  border-radius: 50%;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  transition: 0.4s -webkit-filter ease;
  transition: 0.4s filter ease;
  transition: 0.4s filter ease, 0.4s -webkit-filter ease;
}

.Dog .dog-box-img:hover {
  -webkit-filter: none;
          filter: none;
} 

.Dog .underline {
  text-decoration: none;
  border: 4px solid transparent;
  display: inline-block;
  vertical-align: top;
  line-height: 36px;
  text-transform: uppercase;
  color: black;
  letter-spacing: 0.2em;
  text-align: center;
  font-size: 1.4rem;
  margin: 10px;
  position: relative;
}

.underline::after {
  width: 0%;
  height: 4px;
  display: block;
  background-color: #fff;
  content: " ";
  position: absolute;
  top: 34px;
  left: 50%;
  transition: left 0.4s cubic-bezier(0.215, 0.61, 0.355, 1),
    width 0.4s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.Dog:hover .underline::after {
  width: 100%;
  top: 34px;
  left: 0;
}

.dog-card {
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  margin: 0 auto;
}

.dog-card img { 
  max-height: 350px;
  max-height: 330px;
  object-fit: cover;
  object-position: top;
}

